import { clsx } from 'clsx';
import { FC } from 'react';

import { CardProps } from './card.interface';

export const Card: FC<CardProps> = ({ children, image, ...props }) => {
  const padding = image ? 'zep-pt-0 zep-px-0' : 'zep-px-1.5 zep-py-2';

  return (
    <div
      className={clsx(
        padding,
        'zep-outline-none',
        'zep-flex',
        'zep-flex-col',
        'zep-flex-nowrap',
        'zep-items-start',
        'zep-bg-background-light',
        'zep-rounded-8',
        'zep-shadow-sm',
        'hover:zep-shadow-sm',
        '[&>img]:zep-rounded-tl-8',
        '[&>img]:zep-rounded-tr-8',
      )}
      tabIndex={0}
      data-testid="zep-card"
      {...props}
    >
      {image}
      {image ? (
        <div className="zep-flex zep-h-full zep-flex-col zep-flex-nowrap zep-items-start zep-px-1.5 zep-py-2">
          {children}
        </div>
      ) : (
        children
      )}
    </div>
  );
};
